import { NextResponse } from 'next/server';
import { prisma } from '@/lib/prisma';

interface ServiceCategory {
  id: number;
  name: string;
  slug: string;
  icon: string | null;
  parent_id: number | null;
  order: number;
  children?: ServiceCategory[];
}

interface MenuItem {
  id: string;
  title: string;
  icon?: string;
  children?: MenuItem[];
}

// Функция для рекурсивного построения дерева меню
function buildMenuTree(categories: ServiceCategory[]): MenuItem[] {
  return categories.map((category) => {
    const menuItem: MenuItem = {
      id: category.slug,
      title: category.name,
    };

    // Добавляем иконку только для корневых элементов
    if (!category.parent_id && category.icon) {
      menuItem.icon = category.icon;
    }

    // Добавляем только подкатегории в меню
    // Услуги будут отображаться на странице категории, а не в меню
    if (category.children && category.children.length > 0) {
      menuItem.children = buildMenuTree(category.children);
    }

    return menuItem;
  });
}

// GET - получение динамического меню услуг
export async function GET() {
  try {
    // Рекурсивно загружаем дерево категорий (без услуг для оптимизации)
    // @ts-ignore - ServiceCategory будет доступна после npx prisma generate
    const categories = await prisma.serviceCategory.findMany({
      where: {
        is_active: true,
        parent_id: null, // Только корневые категории
      },
      orderBy: [
        { order: 'asc' },
        { name: 'asc' },
      ],
      include: {
        children: {
          where: { is_active: true },
          orderBy: [
            { order: 'asc' },
            { name: 'asc' },
          ],
          include: {
            children: {
              where: { is_active: true },
              orderBy: [
                { order: 'asc' },
                { name: 'asc' },
              ],
            },
          },
        },
      },
    });

    const menuData = buildMenuTree(categories as ServiceCategory[]);

    return NextResponse.json({ menuData });
  } catch (error) {
    console.error('Error fetching services menu:', error);
    return NextResponse.json(
      { error: 'Failed to fetch services menu' },
      { status: 500 }
    );
  }
}
